@import url('https://fonts.googleapis.com/css2?family=Grenze+Gotisch:wght@300&display=swap');

.logo{
	float: left;
	width: fit-content;
	position:fixed;
	margin-bottom:all; 
    z-index: 1000;
    top: 110px;
}
.title{
	font-family: 'Grenze Gotisch', cursive;
	width: fit-content;
	font-size:1.7em;
	display: block;
}
.margin-container{
	margin-left: 350px;
}
.container{
	margin-top: 110px;
}

@media screen and (max-width: 500px){

.logo{
	float: left;
	width: fit-content;
	position:sticky;
	margin-bottom:2px; 
    z-index: 0;
    top:110px !important;
}
.margin-container{
	margin-left: 0px;
}
.container{
	margin-top: 0px !important;
}
.mb-5{
	margin-bottom: 10px !important;
}
.ml-5{
	margin-left: 0px !important;
}
.body-container{
	margin-top: 450px !important;
}
}
